	module.exports = {"data":{"markdownRemark":{"html":"<p>수업 시간에 Nietzsche에 대해 계속 이야기하십시오. 니체의 교리는 실제로 가치가있다. 왜냐하면 세계 질서가 바뀌어야하는 시대에 정치적 사고를 내 놓았 기 때문이다. 그의 목적은 새로운 질서를 세우는 것이고, 결국 \"수퍼맨\"에 고정시켰다. 그러한 요구가 역사에서 일어 났을 때, 즉 새로운 질서를 수립 할 필요가있을 때, 힘을 사용하고 강건한 사람을 통해 새로운 질서를 수립하는 것을 주장하는 사상가가 항상있을 것입니다. 문제는 새로운 질서가 수립 될 수 있는지, 또는 그것이 얼마나 열심히 사람들이 일을 하든지에 상관없이 자체 발전의 논리를 갖고 있는지 여부입니다. 마침내 그것은 그러한 질서입니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0076.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0076.md","frontmatter":{"title":"4월 14일 목요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0076.md absPath of file >>> MarkdownRemark"}}

